.form{
    display: grid;
    grid-template-columns: 1fr auto;
    grid-gap: 20px;
    margin: 10px;
    padding: 10px;
}
@media(max-width:767px){
    .form{
        grid-template-columns: 1fr;
    }
}
.form__button{
    padding: 10px;
    background-color: teal;
    color: white;
    border: none;
    transition: 1s;
   
}
.form__button:hover{
    background-color: hsl(180, 100%, 35%);
    transform: scale(1.2);
}
.form__button:active{
    background-color: hsl(180, 100%, 45%);
}
